  .backPage{
    position: absolute;
    top: 20px;
    left: 15px;
}
.check{
    animation-name: FadeInLeft;
    animation-duration: 1s;
    width: 100%;
    display: block;
    margin: 0 auto;
    min-height: 100vh;
    .swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction{
  bottom: 563px;

    }
    .swiper-slide{
        transform: translateX(-50px);
    }
    .swiper-slide.swiper-slide-prev{
        transform: translateX(50px);
    }
    .swiper-slide.swiper-slide-active{
        transform: translateX(0);
    }
    .swiper-pagination-bullet-active{
        background: #000000;
        width: 14px;
        border-radius: 9px;
    }
    &__sum{
        text-align: center;
        padding-top: 15px;
        p{
            font-weight: 300;
        }
    }
    &__content{
    
        display: block;
        margin:  0 auto;
        background: white;
        height: 545px;
        margin-top: 37px;
        position: relative;
        width: 545px;
        &::before {
          
            content: "";
            /* В любом случае необходим, даже пустой. Иначе не будет работать */
           
         z-index: -1;
            position: absolute;
         left: 0;
          top: -16px;
          
           
          width: 100%;
          height: 16px;
          background: url(../images/koreshok.svg);
          
          background-size: cover;
        }

    }
    &__top{
        width: 100%;
        background-color: #FDFAFF;
        padding-top: 2px;
        padding-bottom: 13px;
        h3{
            color: #8684B9;
            text-align: center;
            font-size: 13px;
            font-weight: 700;
        }
    }
    &__middle{
        width: 90%;
        display: block;
        margin: 0 auto;
        &-item{
            margin-top: 18px;
            display: flex;
            h3{
                font-size: 13px;
                font-weight: 300;
                width: 70%;
            }
            p{
                width: 30%;
                font-size: 13px;
                font-weight: 300;
                text-align: right;
            }
        }
    }
}

.button-href{
    width: 100%;
    display: flex;
    justify-content: center;
}
.check__button{
    margin: 0 auto;
    margin-top: 14px;
    width: 90%;
height: 53px;
left: 17px;
top: 574px;
width: 326px;
background: linear-gradient(90deg, #FFE486 0%, #FFCF86 100%);
// box-shadow: 0px 4px 4px #8684B9;
border-radius: 8px;
font-size: 13px;
font-weight: 700;
color: black;
border: none;
}

@media(max-width: 576px){
    .check__content{
        width: 90%;
        
    }
    .check__button{
        margin-top: 14px;
        width: 90%;
    }
}