.teaPerson{
    &__item{
  
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        margin: 0 auto;
        margin-top: 5%;
        padding-bottom: 14px;
        border-bottom: 1px solid #F1F1F1;
    }
    &__main{
        display: flex;
        align-items: center;
        img{
            object-fit: cover;
            width: 45px;
            height: 45px;
        }
    }
    &__text{
        margin-left: 16px;
        h4{
        margin-top: 0;
            font-size: 16px;
            font-weight: 700;
            margin-bottom: 2px;
        }
        p{
            font-size: 12px;
            font-weight: 300;
        }
    }
}