.callPerson{
 
    height: 100vh;
    &__content{
       animation-name: FadeInLeft;
       animation-duration: 1s;
        height: 271px;
        width: 326px;
        background: linear-gradient(179.73deg, #FDFAFF 15.79%, #FFFFFF 15.8%, #FFFFFF 83.18%, #FDFAFF 83.19%);
        border-radius: 30px;
      
    //    text-align: center;
       margin: 0 auto;
       margin-top: 141px;
       
      display: flex;
      align-items: center;
       
    }
}


@media(max-width:768px){
    .callPerson__content{
        width: 90%;
    }
}