  
    .callPersonSingle{
        height: 100vh;
    }
    .callPersonSingle__content{
       animation-name: FadeInLeft;
       animation-duration: 1s;
        height: 271px;
        width: 326px;
        background: linear-gradient(179.73deg, #FDFAFF 15.79%, #FFFFFF 15.8%, #FFFFFF 83.18%, #FDFAFF 83.19%);
        border-radius: 30px;
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: center;
       text-align: center;
       margin: 0 auto;
       margin-top: 156px;
        h3{
           font-size: 17px;
           font-weight: 700;
           
        }
        p{
            margin-top: 9px;
            font-size: 13px;
            font-weight: 300;
        }
        .button-href{
            width: 185px;
        }
    }

    @media(max-width:768px){
        .callPersonSingle__content{
            width: 90%;

        }
    }
   