body{
    margin: 0;
    padding: 0;
    overflow: hidden;
    background: url(./images/screen.svg);
    background-size: cover;
    font-family: 'Lato', sans-serif ; 
  }
  p{
    padding: 0;
    margin: 0;
  }
  ul{
    margin: 0;
    padding: 0;
  }
h1,h2,h3{
  margin: 0;
}






@media(max-height: 800px){
  body{
    overflow: visible;
  }
}
@font-face {
    font-family: 'Lato';
    src: url('./font/Lato-Light.eot');
    src: url('./font/Lato-Light.eot?#iefix') format('embedded-opentype'),
        url('./font/Lato-Light.woff2') format('woff2'),
        url('./font/Lato-Light.woff') format('woff'),
        url('./font/Lato-Light.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Lato';
    src: url('./font/Lato-Regular.eot');
    src: url('./font/Lato-Regular.eot?#iefix') format('embedded-opentype'),
        url('./font/Lato-Regular.woff2') format('woff2'),
        url('./font/Lato-Regular.woff') format('woff'),
        url('./font/Lato-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Lato';
    src: url('./font/Lato-Bold.eot');
    src: url('./font/Lato-Bold.eot?#iefix') format('embedded-opentype'),
        url('./font/Lato-Bold.woff2') format('woff2'),
        url('./font/Lato-Bold.woff') format('woff'),
        url('./font/Lato-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
  font-family: 'Lato';

  src: url('./font/latoblack.woff2') format('woff2'),
      url('./font/latoblack.woff') format('woff'),
      url('./font/latoblack.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
