.main{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.main__content{
    animation-name: FadeInLeft;
    animation-duration: 1s;
    background: white;
    border-radius: 30px;
    height: 507px;
   
}
a{
    color: black;
    text-decoration: none;
    &:hover{
        color: black;
        text-decoration: none;
    }
}
.main__top-content{

    display: flex;
    flex-direction: column;
    align-items: center;
    background:  #FDFAFF;
    border-radius: 30px 30px 0px 0px;
    padding-top: 23px;
    padding-bottom: 23px;
    h1{
        font-weight: 900;
        font-size: 17px;
        color: #8684B9;
        padding-top: 11px;
        padding-bottom: 5px;
    }
    p{
        font-weight: 300;
        font-size: 12px;
    
    }
}

.main__middle{
padding-left: 25px;
padding-right: 25px;
a:last-child .main__middle-item {
    border-bottom: none !important;
}
&-item{
    display: flex;
    border-bottom: 1px solid #F1F1F1 !important;
    padding-bottom: 13px;

    padding-top: 13px;
    img{
        padding-right: 16px;
    }
    h2{
        font-size: 14px;
        font-weight: 700;
    }
    p{
        font-weight: 300;
font-size: 11px;
    }
}
}

.main__bottom{
    background: #FDFAFF;
    border-radius: 0px 0px 30px 30px;
    height: 66px;
    padding-left: 69px;
    padding-right: 69px;
    .bottom__image{
        
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
    }
}
@media(max-width:576px){
.main__content{
    width: 90%;
}
}