.ReadyPay{
 width: 100%;
 height: 100vh;
 display: flex;
 align-items: center;
 justify-content: center;   
 &__content{
    animation-name: FadeInLeft;
    animation-duration: 1s;
     height: 271px;
     width: 326px;
     background: linear-gradient(179.73deg, #FDFAFF 15.79%, #FFFFFF 15.8%, #FFFFFF 83.18%, #FDFAFF 83.19%);
     border-radius: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
     h3{
        font-size: 17px;
        font-weight: 700;
        
     }
     p{
         margin-top: 9px;
         font-size: 13px;
         font-weight: 300;
     }
     .button-href{
         width: 185px;
     }
 }
}
.button-pay{
    margin-top: 23px;
}
@media(max-width:768px){
    .ReadyPay__content{
        width: 90%;
    }
}
