.feedback{
    min-height: 100vh;
.feedback__image{
    padding: 52px;
    display: flex;
    justify-content: center;
}
    &__parag{
        color: black;
        width: 70%;
   display: block;
   margin: 0 auto;
        p{
            text-align: center;
            font-size: 12px;
            font-weight: 400;
            padding-top: 20px;
            padding-bottom: 20px;
        }
    }
&__content{
    animation-name: FadeInLeft;
    animation-duration: 1s;
    background: white;
    border-radius: 30px;
    height: 329px;
    width: 330px; 
    margin: 0 auto;
    border-radius: 30px;

}
&__top{
    background: #FFFBF4;
    border-radius: 30px 30px 0px 0px;
    padding: 13px  40px 16px 38px;
    h3{
        font-size: 16px;
        font-weight: 700;
        text-align: center;
    }
}
&__stars-text{
    width: 67%;
    margin: 0 auto;
    margin-top: 11px;
    display: flex;
    justify-content: space-between;
    p{
        font-size: 12px;
        font-weight: 30;
        
    }
}
&__input{
    margin-top: 43px;
    input{
        height: 40px;
        width: 67%;
        display: block;
        margin: 0 auto;
        background: none;
        border: none;
        border-bottom: 1px solid #FFCF86;
        &::placeholder{
            color: black;
            font-size: 13px;
            font-weight: 300;
        }
    }
}

&__bottom{
    background: #FFFBF4;
    border-radius: 0px 0px 30px 30px;
    height: 30px;
    width: 100%;
   margin-top: 18px;
   display: flex;
   justify-content: center;
   align-items: center;
 .circle-long{
width: 14px !important;
   }
   .circle{
       margin-left: 7px;
       border-radius: 100px;
       width: 6px;
       height: 6px;
       background: #FFCF86;
   }
}
&__device{
    display: flex;
    transform: translateY(8px);
    img{
        margin-right: 17px;
    }
}
&__last{
    
    display: flex;
    align-items: center;
    width: 326px; 
    margin: 0 auto;
    margin-top: 23px;
    justify-content: space-between;
    img{
        width: 50px;
        height: 50px;
    }
    .check__button{
        margin-left: 0;
        margin-right: 0;
        width: 185px;
        height: 50px;
    }
}
&__conditions{
    width: 100%;

    margin-top: 12px;
    // float: right;
    text-align: center;
    margin-right: 45px;
    p{
        color: black;
        font-weight: 500;
        font-size: 10px;

    }
    a{
        text-decoration: underline;
        color: black;
        &:hover{
            color: black;
        }
    }
}
    /*Stars*/
    .rating-area {
        overflow: hidden;
        width: 269px;
        margin: 0 auto;
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
    }
    .rating-area:not(:checked) > input {
        display: none;
    }
    .rating-area:not(:checked) > label {
        float: right;
        width: 64px;
        padding: 0;
        cursor: pointer;
        font-size: 64px;
        line-height: 62px;
        color: lightgrey;
        text-shadow: 1px 1px #bbb;
        position: relative;
    }
    .rating-area:not(:checked) > label:before {
        content: ' ';

      
    //     position: absolute;
    //  left: 10%;
    //   top: 15%;
  
        width: 41px;
        height: 41px;
      
    
        background: url(../images/star.svg) no-repeat center;
        background-size: 41px 41px;
    }
    .rating-area > input:checked ~ label:before {
        background: url(../images/srarGold.svg) no-repeat center;
    }
    .rating-area > input:checked ~ label {
        color: gold;
        text-shadow: 1px 1px #c60;
    }
    .rating-area:not(:checked) > label:hover,
    .rating-area:not(:checked) > label:hover ~ label {
        color: gold;
    }
    .rating-area > input:checked + label:hover,
    .rating-area > input:checked + label:hover ~ label,
    .rating-area > input:checked ~ label:hover,
    .rating-area > input:checked ~ label:hover ~ label,
    .rating-area > label:hover ~ input:checked ~ label {
        color: gold;
        text-shadow: 1px 1px goldenrod;
    }
    .rate-area > label:active {
        position: relative;
    }


}

.feedback__content.middle-sank{
    margin-top: 93px;
}
.sank-button{
    margin-top: 23px;
    width: 100%;
    display: flex;
    justify-content: center;
    .check__button{
        width: 185px;

    }
}

.feedSank__top{
    padding: 33px 80px 34px 79px;
}
.feedContent__sank{
    height: 315px;
}
@media(max-width:576px){
.feedback__conditions{
    float: right;
    width: 158px;
    text-align: left;
}
    .feedback__content{
          width: 90%;
    }
    .feedback__last{
        width: 90%;
    }
}