
  
  @keyframes TransformBottom {
    from {
    
        transform: translateY(100%);
  
    }
  
    to {
        transform: translateY(0%);

    }
}
.tea{
height: 100vh;
    &__content{
        animation-name: TransformBottom;
        animation-duration: 1s;
        animation-fill-mode: forwards;
        margin-top: 16px;
        height: 100%;
        background: white;
        border-radius: 30px 30px 0px 0px;
        position: relative;
        transform: translateY(100%);
    }
    &__top{
        height: 40px;
        border-radius: 30px 30px 0px 0px;
        background: #FFFBF4;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .top__line{
        cursor: pointer;
        width: 38px; 
        height: 4px;
        background: #FFCF86;
        border-radius: 9px;
    }
    &__middle{
        margin-top: 19px;
  
        h2{
            font-weight: 700;
font-size: 16px;
text-align: center;
        }
    }
    &__input{
        position: absolute;
       top: 105px;
       width: 100%;
            display: flex;
            justify-content: center;
           
            input{

                width: 150px;
                background: none;
                border: none;
                border-bottom: 1px solid #FFCF86;
                font-weight: 400;
font-size: 18px;
text-align: center;
color: black;
&:focus{
    outline: none;
}
            }
    }
    &__close{
       transform: translateX(21px);
    }
}
.choice__sum{
width: 80%;
display: block;
margin: 0 auto;
margin-top: 102px;
margin-bottom: 128px;
}
.choice__circles{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.choice__circle{
    width: 90px;
    height: 90px;
    border-radius: 100px;
    background: #FFF0D9;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    h3{
        font-size: 15px;font-weight: 700;
        text-align: center;
    }
    p{
        margin-top: 5px;
        text-align: center;
        font-size: 12px;
        font-weight: 300;
    }
}
.choice__buttons{
    display: flex;
    justify-content: space-between;
    margin-left: 19px;
    margin-right: 19px;
}
.choice__button{
    width: 128px;
height: 42px;
background: #FFF0D9;
border-radius: 25px;
font-weight: 700;
font-size: 11px;
text-transform: uppercase;
border: none;
margin-top: 25px;
color: black;
}

.tea__bottom{
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 21%;
}
@media(min-width:768px){
    .tea__content{
     
        width: 550px;
        height: 645px;
        display: block;
        margin: 0 auto;
    }
    
}